import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { IoIosArrowForward  } from "react-icons/io";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import furniture from './funiture.json';
import FunitureItem from './FunitureItem';

import {FiArrowRight, FiArrowLeft} from "react-icons/fi"; 

const SalesectionBlock = styled.div`
    margin-top:100px;
    .inner {
         font-weight:300; 
        .salemenu { 
            font-size:14px;
            padding-bottom:15px; border-bottom:1px solid #ddd;
            a { padding-right:10px; &:hover { text-decoration:underline; } }
            span { padding-left:10px; }
        }
        .salecont1 {
            .saletop {
                h2 { font-size:36px; padding:60px 0 30px; }
                p:nth-of-type(1) { width:60%; }
                p:nth-of-type(2) { padding:50px 0; cursor:pointer; text-decoration:underline; span{padding:0 10px;} }
            }
            .svbox {
                display:flex; {
                    .saletxt {
                        width:45%; margin-left:2%;
                        h3 { font-size:25px;}
                        p:nth-of-type(1) { font-size:14px; font-weight:600; padding:20px 0 40px; }
                        p { line-height: 2; span {font-weight:600;}}
                    }
                }
            }
            .salelist {
                padding-bottom:100px;
                h2 { padding: 60px 0 40px; font-size: 24px; font-weight: 600; }
                .slick-arrow {
                    position:absolute; top:40%; transform:translateY(-50%);
                    background:#000; padding:10px; z-index:9; width:50px; height:50px; color:#fff;
                    border-radius: 50%;
                }
                .slick-prev{ left: 0;}
                .slick-next { right: 0;}
                .slick-dots { 
                    position:absolute; bottom: -40px; width: 100%; 
                    li {
                        display: inline-block; width: 33.33%; height: 3px;
                        &.slick-active  {
                            button {  background: #000; }
                        }
                        button { 
                            color: transparent; text-indent: -999999; overflow: hidden; width: 100%; background: #ddd;
                            height: 3px; transition: all 0.5s;
                        }
                    }
                }
            }
            .saleul {
                padding-bottom:100px; 
                ul {
                    padding:20px 0;
                    list-style: disc; padding-left:30px;
                    li { line-height:2.5; }    
                }
            }
            
        }
    }
`;

const Salesection = () => {
    const settings = {
        dots:true,
        infinite:true,
        speed:600,
        autoplay:false,
        autoplaySpeed:4000,
        slidesToShow:4,
        slidesToScroll:4,
        prevArrow: <FiArrowLeft />, 
        nextArrow: <FiArrowRight />
    } 

    return (
        <SalesectionBlock>
            <div className="inner">
                <div className="salemenu">
                    <p>
                        <Link to="/">홈</Link><IoIosArrowForward /><span>할인 중</span>
                    </p>
                </div>
                <div className="salecont1">
                    <div className="saletop">
                        <h2>할인 중</h2>
                        <p>거리 두기와 함께 멀어진 설렘, 지금 IKEA와 함께 찾아봐요! 매일 아침 나를 감싸는 침구 한 장의 포근함, 깔끔하게 정리된 옷들을 보고 ‘오늘은 무슨 옷을 입을까?’ 고민하는 순간, 침실에서의 모든 순간이 설레게 될 거에요.</p>
                        <p>
                            침실가구 및 액세서리 최대 15% 할인<span>|</span>
                            맘편한 푸드바우처 증정<span>|</span>
                            배송비 할인<span>|</span>
                            초콜릿 증정
                        </p>
                    </div>
                    <div className="svbox">
                        <div className="salevideo">
                            <iframe width="788" height="433" src="https://www.youtube.com/embed/17SonGgZ88U" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                        </div>
                        <div className="saletxt">
                            <h3>설레는 우리집,<br /> 500여개 침실가구 및 액세서리 최대 15% 할인</h3>
                            <p>행사 기간: 5월 6일 - 5월 19일</p>
                            <p>하루를 시작하고 마무리 하는 공간인 침실에서 IKEA 제품들로 설렘을 더해보는 건 어때요?</p>
                            <p><span>IKEA Family</span>라면 지금 500여개의 침실가구 및 액세서리를 최대 15% 할인된 가격으로 만날 수 있어요.</p>
                        </div>
                    </div>
                    <div className="salelist">
                        <h2>침실 가구</h2>
                        <Slider {...settings}>
                            {
                                furniture.map( (data, index) => <FunitureItem className="slide" key={index} name={data.name} photo={data.photo} photo2={data.photo2} description={data.description} price={data.price} score={data.score} explain={data.explain} />)
                            }
                        </Slider>
                    </div>
                    <div className="saleul">
                        <h3>[이용약관]</h3>
                        <ul>
                            <li>본 행사는 5월 6일부터 5월 19일까지 진행되며 IKEA 모든 매장과 IKEA 공식 온라인몰에서 진행됩니다.</li>
                            <li>본 행사는 할인 태그가 달린 일부 제품에 한하여 15% 할인 적용 가능합니다. IKEA 매장과 IKEA 공식 온라인몰 전 제품이 아닌 일부 제품으로 한정됩니다.</li>
                            <li>본 행사의 혜택은 1일 1회에 한하며, 1인당 제품별 최대 10개 구매 제한 있습니다. 매장과 온라인 재고 상황에 따라 구매 제한 수량이 다를 수 있습니다.</li>
                            <li>본 행사에 포함된 혜택 내용은 ‘행사 기간’ 동안 제품을 구매하는 경우에만 유효합니다.</li>
                            <li>본 행사는 IKEA FAMILY 멤버에게만 제공되며, IKEA 공식 온라인몰에서 무료가입 가능합니다.</li>
                            <li>재고 소진 제품에 대해 별도로 공지하지 않으며, 소진 시 행사가 중단될 수 있습니다. 예약 구매는 불가능하며, 재입고 여부는 고객 지원 센터 문의 전화 등으로 확인이 어렵습니다.</li>
                            <li>일부 제품은 조립이 필요할 수 있으며, 조립 및 배송 서비스 비용에는 할인 조건이 적용되지 않습니다.</li>
                            <li>안내된 할인 조건(대상 제품, 할인율, 기간 및 수량 등)은 IKEA 내부 정책 및 사정에 의해 사전 공지 없이 변경 또는 철회될 수 있습니다.</li>
                            <li>본 행사에 해당하는 제품의 교환 또는 환불 시에 구매 영수증을 반드시 제시해야 가능합니다.</li>
                        </ul> 
                    </div>
                </div>
                
            </div>
        </SalesectionBlock>
    );
};

export default Salesection;